body {
  font-family: 'Dosis', sans-serif;
  /* Documentation site component styles */ }
  body .navigation {
    float: left;
    width: 250px;
    list-style-type: none;
    padding: 0; }
    body .navigation li {
      padding: 10px;
      background-color: #607d8b;
      margin: 10px 0; }
    body .navigation a, body .navigation a:visited {
      text-decoration: none;
      color: #FFFCF9;
      font-weight: 900;
      letter-spacing: 1px; }
  body .props {
    border-collapse: collapse; }
    body .props td {
      border-top: solid 1px #FFD166; }
    body .props td, body .props th {
      padding: 10px; }
    body .props th, body .props td {
      text-align: left;
      padding: 5px; }
    body .props tr:nth-of-type(2n) {
      background-color: #FFD166; }
    body .props td:last-of-type {
      text-align: center; }
  body .example {
    border: solid 2px #607d8b;
    padding: 16px;
    margin-bottom: 16px; }
    body .example h4:first-of-type {
      margin-top: 0; }
    body .example .showCodeBtn {
      margin: 20px 0; }
      body .example .showCodeBtn a {
        text-decoration: none;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: white;
        background-color: #607d8b;
        padding: 10px; }
  body .componentpage {
    margin-left: 275px; }
  body .clear {
    clear: both; }



